.timeBarWrapper {
    max-width: 1280px;
    margin: 0 auto !important;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.timeBarHeading {
    text-align: left;
    padding: 0 10%;
}

.timeBarNumber {
    display: inline-block;
    font-size: 32px;
    font-size: 2rem;
    font-family: 'Proza Libre';
    min-width: 100px;
    margin-left: 10px;
}

@media (max-width: 992px) {

    .timeBarNumber {
        font-size: 1.2rem;
        margin-left: 0;
    }
}

.timeBarTitle {
    display: inline-block;
    font-size: 28.8px;
    font-size: 1.8rem;
    margin-left: 10px;
}

@media (max-width: 992px) {

    .timeBarTitle {
        font-size: 1rem;
    }
}

.timeBar {
    text-align: center;
    margin-bottom: 50px;
}

.progressBar {

}

.timeBarButton {
    margin-bottom: 50px;
}


